* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  @media screen and (min-width: 0px) and (max-width:600px) {
    .Responsive {
      width: 100%;
      display: flex;
      flex-direction: column;
    }
    .input-field{
      width: 500px;
    }
  }
  
  @media screen and (min-width: 0px) and (max-width: 827px) {
    .btnresponsive {
      position: unset;
      margin-top: 500px;
      margin-left: 120px;
    }
  }
  
  @media screen and (min-width: 900px) and (max-width: 1600px) {
    .keyResponsive {
      width: 50%;
    }
  }
  